"use client";
import cookieIcon from "@/assets/logo/cookie.png";
import { Icon } from "@iconify/react";
import Image from "next/image";
import FadeInSection from "../animation/FadeEffect";
export default function Hero() {
  return (
    <section className="container min-h-screen relative  ">
      <div className="bg-[#4C4A4A5E] fixed top-0 left-0 w-full h-screen">
        <div className="bg-[#02D5E8] fixed bottom-0 left-0 w-full h-20 md:h-28 shadow-[0px_4px_19.6px_4px_#00000040_inset] border-t-8 border-[#D9D9D9] text-black font-bold pt-4 text-center ">
          <Image
            src={cookieIcon}
            alt="Cookie Icon"
            width={200}
            height={200}
            className="absolute left-50 transform -translate-x-1/2 -top-10 md:-top-16 "
          />
          <div className="flex justify-center gap-8 items-center">
            <p>Our website uses cookirs to enhance your browsing experience.</p>
            <button className="bg-white px-4 py-1 rounded-lg">Accept</button>
          </div>
        </div>
      </div>

      <div className="grid  justify-center pt-48 h-[70vh] md:h-[90vh]">
        <div className="wrapper text-center ">
          <FadeInSection
            initial={{ opacity: 0, x: -100 }}
            scrollTop={{ opacity: 1, x: 0 }}
            scrollBottom={{ opacity: 0, x: -80 }}
            margin="400px 0px -100px 0px"
          >
            <h1 className="text-2xl sm:text-3xl lg:text-7xl xl:text-8xl font-russo-one leading-[149%] bg-[linear-gradient(180deg,#FFFFFF_0%,#999999_100%)] text-transparent bg-clip-text ">
              We Power Your Growth
            </h1>
            <h3 className="my-10 text-2xl xl:text-5xl bg-[linear-gradient(90deg,#FFFFFF_0%,#02D5E8_100%)] bg-clip-text text-transparent font-medium">
              Your extended tech and operations team <br /> without the
              overhead.
            </h3>
            <p className=" text-base xl:text-lg">
              We connect your tools, streamline workflows, and ship work that
              moves revenue.
            </p>
            <div className="flex gap-8 justify-center mt-8">
              <button className="flex justify-center items-center gap-8 border px-5 py-2 bg-gradient-to-l from-blue-light to-dark-white text-black rounded-lg">
                Discover
                <Icon
                  icon="meteor-icons:arrow-up-right"
                  width="21"
                  height="21"
                />
              </button>
              <button className="flex justify-center items-center gap-8 border px-5 py-2 bg-gradient-to-l from-blue-light to-dark-white text-black rounded-lg">
                Book a call
                <Icon
                  icon="meteor-icons:arrow-up-right"
                  width="21"
                  height="21"
                />
              </button>
            </div>
          </FadeInSection>
        </div>
      </div>
    </section>
  );
}
